@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800');

@import url('https://fonts.googleapis.com/css?family=Source+Code+Pro:200,300,400,500,600,700,900');

/**************************************/

/*** Screen Sizes Mixins ***/

// 512
@mixin xs {
    @media (max-width: 511px) {
        @content;
    }
}

// 768
@mixin sm {
    @media (max-width: 799px) {
        @content;
    }
}

// 1024
@mixin md {
    @media (max-width: 1023px) {
        @content;
    }
}

// 1440
@mixin lg {
    @media (max-width: 1279px) {
        @content;
    }
}

/**************************************/

/*** Colours Variables ***/

/*main*/

$m-01: #698fb7;

/*neutrals*/

$neu-01: #f9f9f9;
$neu-01a: #f6f6f6;
$neu-02: #f0f0f0;
$neu-03: #ccc;
$neu-04: #848484;
$neu-05: #525252;
$neu-06: #4a4a4a;
$neu-07: #383838;


$font-01:'Open Sans',
sans-serif;
$font-02: 'Source Code Pro',
monospace;



/* --------------------------*/

/* ----- MAIN ELEMENTS ----- */

/* --------------------------*/

/* // General Elements \\  */

body {
    font-family: sans-serif;
    font-family: $font-01;
    font-weight: 200;
    margin: 0;
    padding: 0;
    font-size: 15px;
    transition: all .3s ease-in-out;
    -webkit-tap-highlight-color: transparent;
}

#wrapper {
    width: 100%;
    margin: 0;
    padding: 0;
    position: relative;
    transition: all .3s ease-in-out;

}


/* // Selected Elements \\  */

::-moz-selection {
    background-color: $m-01;
    color: #ffffff;
}

::selection {
    background-color: $m-01;
    color: #ffffff;
}


/*  // Components \\  */

button {
    color: $neu-04;
    display: inline-block;
    padding: 15 15 15 18;
    text-align: center;
    letter-spacing: 5px;
    background-color: $neu-01a;
    transition: all .6s ease;
    font-size: 12px;
    cursor: pointer;
    position: relative;
    z-index: 1;
    font-family: $font-02;
    font-weight: 500;
    border: 0;
    margin: 0 auto;
    margin-top: 40;
    display: flex;
    justify-content: center;

    &:after {
        content: '';
        width: 0%;
        height: 2px;
        background-color: $m-01;
        border-radius: 20px;
        position: absolute;
        bottom: 0;
        transition: all 0.7s ease;

    }

    &:before {
        content: '';
        position: absolute;
        background-color: #fff;
        width: 0%;
        height: 100%;
        z-index: -1;
        top: 0;
        transition: all 0.5s ease;
    }

    &:hover {
        color: $m-01;

        @include sm {
            color: $neu-04;
        }

        &:after {
            width: 30%;

            @include sm {
                display: none;
            }
        }

        &:before {
            width: 100%;

            @include sm {
                display: none;
            }
        }
    }

    &:focus {
        outline: 0;
    }

}

a {
    text-decoration: none;
    color: inherit;
    cursor: pointer;
}

p {
    color: $neu-04;
    font-size: 16px;
    padding: 0;
    margin: 0;
    line-height: 1.8;
    letter-spacing: 0.7;
    transition: all 0.3s ease;

    @include sm {
        font-size: 14px;
    }
}

.links {
    color: $m-01;
    position: relative;
    font-family: $font-02;
    font-weight: 300;
    display: inline-flex;
    justify-content: center;

    &:after {
        content: '';
        width: 0%;
        height: 2px;
        border-radius: 20px;
        background-color: $m-01;
        position: absolute;
        bottom: -2px;
        transition: all 0.3s ease;

        @include sm {
            width: 0;
            text-decoration: underline;
        }
    }

    &:hover {
        &:after {
            width: 100%;
        }
    }
}

/*  // Texts \\  */

.main-title {
    font-size: 35px;
    color: $neu-04;
    font-family: $font-01;
    font-weight: 100;
    letter-spacing: 10px;
    text-align: center;
    margin: 0;
    margin-bottom: 60px;
    text-transform: uppercase;
    transition: all .5s ease;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 0 0 20px 0;

    &.white {
        color: #fff;
    }

    @include sm {
        font-size: 25px;
        margin-bottom: 40px;
    }

    &:after {
        background-color: $m-01;
        width: 80px;
        content: '';
        height: 2px;
        border-radius: 20px;
        position: absolute;
        transition: all .5s ease;
        bottom: 0px;

        @include sm {
            width: 50px;
        }
    }
}

.main-text {
    max-width: 800px;
    text-align: center;
    margin: 0 auto;

    @include sm {
        padding: 0 10px;
    }

    &.left {
        text-align: left;
    }
}


#scrollme {
    margin: 0 auto;
    height: 50px;
    cursor: pointer;
    width: 120px;

    .backtop {
        position: relative;
        color: $m-01;

        p {
            font-size: 12px;
            font-family: $font-02;
            font-weight: 400;
            letter-spacing: 2px;
            margin-bottom: 50px;
            opacity: 0;
            transition: all 0.6s ease;
            padding-top: 10px;
            color: $m-01;
            text-align: center;
            margin: 0 auto;
            cursor: pointer;
        }

        span {
            width: 20px;
            height: 2px;
            position: absolute;
            top: 20px;
            left: calc(50% - 10px);
            border-radius: 20px;
            transition: all 0.4s ease-in-out;
            background-color: $m-01;

            @include sm {
                width: 15px;
            }
        }

        span:nth-child(1) {
            transform: rotate(45deg);
            margin-left: 6px;

            @include sm {
                margin-left: 5px;
            }
        }

        span:nth-child(2) {
            transform: rotate(-45deg);
            margin-left: -7px;

            @include sm {
                margin-left: -5px;
            }
        }
    }

    &:hover {
        .backtop {
            p {
                opacity: 1;
                padding-top: 0px;
                font-size: 11px;

                @include sm {
                    display: none;
                }
            }

            span {
                top: 22px;
                width: 20px;

                @include sm {
                    top: 20px;
                }
            }

            span:nth-child(1) {
                left: calc(50% - 5px);
                transform: rotate(180deg);

                @include sm {
                    left: calc(50% - 10px);
                    transform: rotate(45deg);
                    width: 15px;
                    margin-left: 5px;
                }
            }

            span:nth-child(2) {
                right: calc(50% - 5px);
                transform: rotate(-180deg);

                @include sm {
                    right: calc(50% - 10px);
                    transform: rotate(-45deg);
                    width: 15px;
                    margin-left: -5px;

                }
            }
        }
    }
}

/* ---------------------------*/

/* ------- CONTAINERS ------- */

/* ---------------------------*/


.content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: calc(100% - 10%);
    margin: 0 auto;
    transition: all .3s ease-in-out;
}

.content-full {
    padding: 0 5%;

    &.blue {
        background-color: $m-01;
    }

    &.gray {
        background-color: $neu-02
    }

    &.dark {
        background-color: $neu-07;
    }
}

.section,
.section-works {
    width: 100%;
    max-width: 1280px;
    padding: 80px 0;
    transition: all .3s ease-in-out;
    margin: auto;

    @include sm {
        padding: 50px 0;
    }
}


/* ---------------------------*/

/* --------- MODULES -------- */

/* ---------------------------*/

.main-nav {

    .iconmenu {
        width: 40px;
        height: 40px;
        margin: 25px;
        position: fixed;
        z-index: 4;
        cursor: pointer;
        right: 0;

        @include sm {
            margin: 10px;
        }

        .iconopen {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
            background-color: $neu-01;
            border-radius: 100%;

            span {
                position: absolute;
                width: 20px;
                height: 2px;
                border-radius: 20px;
                transition: all 0.3s ease;
                background-color: $m-01;
                display: flex;
                align-items: center;
                justify-content: center;

                &:before,
                &:after {
                    position: absolute;
                    content: '';
                    width: 20px;
                    height: 100%;
                    background-color: inherit;

                }

                &:before {
                    transition: all 0.3s ease;
                    transform: translateY(-6px);
                }

                &:after {
                    transition: all 0.4s ease;
                    transform: translateY(6px);
                }
            }

            &.close {
                transition: all 0.3s ease;
                background-color: transparent;

                span {
                    background-color: $neu-03;
                    width: 0px;

                    &:before {
                        width: 20px;
                        transform: rotate(45deg);
                    }

                    &:after {
                        width: 20px;
                        transform: rotate(-45deg);
                    }
                }
            }
        }

        &:hover {
            .iconopen {
                span {
                    &:before {
                        width: 16px;

                        @include sm {
                            width: 20px;
                        }
                    }

                    &:after {
                        width: 16px;

                        @include sm {
                            width: 20px;
                        }
                    }
                }

                &.close {
                    span {
                        background-color: $neu-04;

                        @include sm {
                            background-color: $neu-03;
                        }

                        &:before {
                            width: 20px;
                        }

                        &:after {
                            width: 20px;
                        }
                    }
                }
            }
        }
    }

    .nav {
        background-color: white;
        margin: 0;
        width: 100%;
        height: 90px;
        position: fixed;
        top: -95px;
        transition: all .5s ease-in;
        box-shadow: 0px -10px 20px black;
        z-index: 3;
        display: flex;
        align-items: center;
        justify-content: center;

        @include sm {
            top: -65px;
            height: 60px;
        }

        &.navshow {
            transition: all .5s ease-out;
            top: 0;
        }

        /*  // Menu List \\  */
        .menu {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-wrap: wrap;
            width: 100%;

            .menu-item {
                color: $neu-04;
                list-style: none;
                width: 30%;
                min-width: 60px;
                max-width: 250px;
                letter-spacing: 4px;
                height: 50px;
                transition: all 0.4s ease;
                position: relative;
                font-family: $font-02;
                font-weight: 400;
                font-size: 14px;
                display: flex;
                align-items: center;
                justify-content: center;

                @include sm {
                    width: 25%;
                    min-width: 80px;
                    font-size: 13px;
                    letter-spacing: 2px;
                    height: 30px;
                }

                &:after {
                    content: '';
                    width: 0%;
                    height: 2px;
                    border-radius: 20px;
                    background-color: $m-01;
                    position: absolute;
                    bottom: 0;
                    transition: all 0.4s ease;
                }

                &:hover {
                    color: $m-01;

                    @include sm {
                        letter-spacing: 2px;
                        color: $neu-04;
                    }

                    &:after {
                        width: 35px;

                        @include sm {
                            width: 0px;
                        }
                    }
                }

                &.current {
                    color: $m-01;

                    &:after {
                        width: 30px;

                        @include sm {
                            width: 20px;
                        }

                    }
                }
            }
        }
    }

}

.footer {
    background-color: #313131;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;



    @include sm {
        height: 100px;
    }

    @include xs {
        flex-direction: column;
    }

    p {
        font-size: 12px;
        letter-spacing: 1.1;
        font-family: $font-02;
        font-weight: 400;
        text-align: center;
        color: $neu-02;

        &:first-child {
            color: $neu-04;
            margin-right: 10px;

            @include xs {
                margin: 0;
            }
        }
    }
}

.header {
    width: 100%;
    margin: 0 auto;
    height: 650px;
    transition: all 0.6s ease-in-out;
    background-color: $neu-03;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

    @include lg {
        height: 600px;
    }

    @include md {
        height: 500px;
    }

    @include sm {
        height: 360px;
        background-size: 250px, cover;
        background-position: center 50px;
    }

    &:before {
        background-color: $m-01;
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 0;
    }

    span {
        position: absolute;
        top: 0;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        width: 25%;
        height: 100%;
        opacity: 0.5;
        transition: width 0.6s ease;
        z-index: 1;
        -webkit-filter: grayscale(100%);
        /* Safari 6.0 - 9.0 */
        filter: grayscale(100%);

        &:nth-child(1) {
            left: 0;
        }

        &:nth-child(2) {
            left: 25%;
        }

        &:nth-child(3) {
            right: 25%;
        }

        &:nth-child(4) {
            right: 0%;
        }

        &:after {
            position: absolute;
            width: 100%;
            height: 100%;
            content: '';
            left: 0;
            top: 0;
            background-color: rgba(52, 52, 52, 0.85);
            transition: all 0.4s ease-in;
        }

        &:hover {
            width: 40%;
            opacity: 1;
            -webkit-filter: grayscale(0%);
            filter: grayscale(0%);
            z-index: 2;

            @include sm {
                width: 25%;
                -webkit-filter: grayscale(100%);
                filter: grayscale(100%);
                opacity: 0.5;
            }

            &:after {
                background-color: rgba(52, 52, 52, 0.5);

                @include sm {
                    background-color: rgba(52, 52, 52, 0.85);
                }

            }
        }
    }

    .header-content {
        width: 100%;
        z-index: 2;
        position: relative;
        transition: all .6s ease-in-out;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        h1 {
            font-size: 50px;
            color: white;
            font-weight: 100;
            letter-spacing: 2px;
            -webkit-transition: all .6s ease-in-out;
            transition: all .6s ease-in-out;

            @include sm {
                font-size: 35px;
            }
        }

        p {
            color: white;
            font-family: $font-02;
            font-weight: 400;
            letter-spacing: 8;
            font-size: 14px;
            text-align: center;
            z-index: 2;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;

            &:before {
                content: '';
                width: 160px;
                height: 6px;
                position: absolute;
                background-color: $m-01;
                z-index: -1;
                transform: translateX(-4px);
            }
        }
    }

    &:hover {
        .header-content {
            opacity: 0.2;

            @include sm {
                opacity: 1;
            }
        }
    }
}

.works-container {


    .btn-filter {
        display: none;

        @include sm {
            display: flex;
            margin-bottom: 30px;
            justify-content: space-between;
            align-items: center;

        }

        .label {
            display: none;

            @include sm {
                display: none;
                width: 100%;
                text-align: center;
                font-family: $font-02;
                font-weight: 400;
                color: $m-01;
                margin-left: 20px;
                font-size: 12px;
                letter-spacing: 1px;
            }
        }


    }



    .filters-container {

        .filters {
            display: flex;
            justify-content: space-around;
            margin-bottom: 60px;


            .filter {
                letter-spacing: 1px;
                padding: 8px 10px;
                border-radius: 2px;
                color: $m-01;
                display: flex;
                align-items: center;
                margin-top: 0;

                &.active-btn {
                    background-color: $m-01;
                    color: white;

                    &:hover {
                        color: white;
                    }
                }

                &:before {
                    content: '';
                    position: absolute;
                    background-color: $m-01;
                    width: 0%;
                    height: 100%;
                    z-index: -1;
                    top: 0;
                    transition: all 0.5s ease;
                    border-radius: 2px;
                }

                span {
                    background-color: white;
                    position: absolute;
                    border-radius: 3px;
                    top: 45px;
                    width: 25px;
                    height: 25px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.5);
                    color: $m-01;
                    transition: all 0.5s ease;
                    opacity: 0;
                    transform: translateY(10px);

                    @include sm {
                        //position: initial;

                        opacity: 1;
                        border: 1px solid $neu-02;
                        right: -40px;
                        transform: translateX(0);
                        top: initial;
                        box-shadow: none;
                    }

                    &:before {
                        content: '';
                        position: absolute;
                        width: 8px;
                        height: 8px;
                        background-color: inherit;
                        top: -3px;
                        transform: rotate(45deg);

                        @include sm {
                            left: -4px;
                            top: initial;
                            border-left: 1px solid $neu-02;
                            border-bottom: 1px solid $neu-02;

                        }
                    }
                }


                &:hover {
                    color: $m-01;

                    @include sm {
                        color: $m-01;
                    }

                    &:before {
                        width: 100%;

                        @include sm {
                            display: none;
                        }
                    }

                    span {
                        opacity: 1;
                        transition: all 0.5s 0.2s ease;
                        transform: translateY(0px);
                        box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.2);

                        @include sm {
                            box-shadow: none;
                        }
                    }
                }




            }

            @include sm {
                display: none;
                margin-bottom: 0px;
            }


        }

        @include sm {
            transform: translateX(120%);
        }

        &.mobile {
            @include sm {
                width: 100%;
                height: 100vh;
                background-color: white;
                position: fixed;
                z-index: 4;
                top: 0;
                right: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                transition: all 0.5s ease-in-out;
                transform: translateX(120%);


                .close-filters {
                    position: absolute;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    top: 30px;
                    right: 30px;
                    width: 40px;
                    height: 40px;
                    cursor: pointer;

                    span {
                        position: absolute;
                        width: 0px;
                        height: 2px;
                        border-radius: 20px;
                        transition: all 0.3s ease;
                        background-color: $neu-03;
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        &:before,
                        &:after {
                            position: absolute;
                            content: '';
                            width: 20px;
                            height: 100%;
                            background-color: inherit;

                        }

                        &:before {

                            transform: rotate(45deg);
                        }

                        &:after {
                            width: 20px;
                            transform: rotate(-45deg);
                        }
                    }

                }

                .filters {
                    flex-direction: column;
                    display: flex;
                    width: 100%;
                    height: 500px;
                    background-color: white;
                    top: 0;
                    transition: all 0.8s 0.5s ease;
                    transform: translateX(100px);
                    opacity: 0;

                    &.anime-tags {
                        opacity: 1;
                        transform: translateX(0px);
                    }

                }

            }
        }

        &.anime-filters {
            @include sm {
                transform: translateX(0%);
            }
        }
    }

    .image-container {
        //display: grid;
        //grid-template-columns: 50% 50%;
        //grid-gap: 20px;
        display: flex;
        justify-content: space-between;
        //background-color: #313131;
        flex-wrap: wrap;

        //position: relative;

        @include sm {
            grid-template-columns: 100%;
        }


        .work {
            display: block;
            width: calc(50% - 20px);
            margin: 10px;

            @include sm {
                width: 100%;
            }

            &.hide {
                animation: hide 6s ease;
                animation-fill-mode: forwards;
                margin: 0px 10px;
                z-index: -1;

                @keyframes hide {

                    50% {
                        height: 0;
                        position: absolute;
                        opacity: 0;
                        display: none;
                    }

                    60% {
                        position: absolute;
                        height: 0;
                        display: none;
                    }


                    100% {

                        opacity: 0;
                        position: absolute;
                        height: 0;
                        display: none;
                    }


                }

                .image {
                    height: 0;
                    padding-bottom: 0%;

                    .img {}
                }


            }

        }

        &.home {

            .work {
                width: calc(33% - 20px);
            }
        }

    }

    .image {

        transition: all .5s ease;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        padding-bottom: 100%;
        position: relative;

        &.big {
            padding-bottom: 50%;

            @include sm {
                width: 100%;
                padding-bottom: 100%;
            }
        }

        .img {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            background-size: cover;
            background-position: center center;
            transition: all 1s ease;
            width: 100%;
            height: 100%;
            top: 0;
        }



        @include sm {
            width: 100%;
            padding-bottom: 100%;
        }

        /* filtro cinza */
        &:before {
            content: '';
            opacity: 0.1;
            background-color: #000;
            width: 100%;
            height: 100%;
            position: absolute;
            transition: all .4s ease;
            z-index: 1;
            top: 0;

        }

        /* filtro azul */
        &:after {
            position: absolute;
            content: '';
            opacity: 0;
            background-color: $m-01;
            transition: all .4s ease;
            width: 90%;
            height: 90%;
            z-index: 1;
            top: 5%;
            border-radius: 20px;
        }

        .overlay-title {
            box-sizing: border-box;
            font-size: 14px;
            line-height: 20px;
            position: absolute;
            font-weight: 500;
            opacity: 0;
            z-index: 2;
            transition: all .4s ease;
            color: white;
            top: calc(50% - 70px);
            transform: translateY(50%);
            text-align: center;
            letter-spacing: 1px;
            text-transform: uppercase;
            padding: 0 20px;
            height: 40px;
            display: flex;
            align-items: flex-end;

            @include sm {
                display: none;
            }
        }

        .overlay-text {
            box-sizing: border-box;
            font-size: 12px;
            line-height: 18px;
            position: absolute;
            opacity: 0;
            z-index: 2;
            font-family: $font-02;
            font-weight: 300;
            transition: all .6s ease;
            color: white;
            bottom: calc(50% - 70px);
            transform: translateY(50%);
            text-align: center;
            padding: 0 20px;
            height: 40px;
            display: flex;
            align-items: flex-start;

            @include sm {
                display: none;
            }

            span {
                opacity: 0.4;
            }
        }


        &:hover {

            .img {
                width: 110%;
                height: 110%;

                @include sm {
                    width: 100%;
                    height: 100%;
                }
            }

            /* filtro cinza */
            &:before {
                opacity: 0.5;

                @include sm {
                    opacity: 0.1;
                }
            }

            /* filtro azul */
            &:after {
                opacity: 0.9;
                width: 100%;
                height: 100%;
                top: 0;
                border-radius: 0px;

                @include sm {
                    display: none;
                }
            }

            .overlay-title {
                opacity: 1;
                transition: all .4s 0.2s ease;
                top: calc(50% - 50px);
            }

            .overlay-text {
                opacity: 0.7;
                transition: all .4s 0.2s ease;
                bottom: calc(50% - 50px);
            }


        }



    }



}

.info-icons {

    .row-icons {
        display: grid;
        grid-template-columns: 16% 16% 16% 16% 16% 16%;
        -webkit-transition: all .3s ease-in-out;
        transition: all .3s ease-in-out;

        @include md {
            grid-template-columns: 33% 33% 33%;

        }

        @include sm {
            grid-template-columns: 50% 50%;
        }

        .icon-section {
            width: 120px;
            text-align: center;
            margin: 0 auto;
            position: relative;
            z-index: 1;

            &:before {
                background-color: white;
                position: absolute;
                content: '';
                border-radius: 1px;
                z-index: 1;

                @include sm {
                    background-color: $m-01;
                }
            }

            &:after {
                background-color: $m-01;
                position: absolute;
                content: '';
