                }

                &:after {
                    width: 0px;
                    height: 9px;
                    top: 87px;
                    left: 25px;
                }

                &:hover {
                    &:after {
                        width: 52px;
                    }
                }
            }

            &:nth-child(3) {
                &:before {
                    width: 17px;
                    height: 27px;
                    top: 58px;
                    left: 66px;
                }

                &:after {
                    width: 17px;
                    height: 0px;
                    top: 58px;
                    left: 66px;
                }

                &:hover {
                    &:after {
                        height: 27px;
                    }
                }
            }

            &:nth-child(4) {
                &:before {
                    width: 17px;
                    height: 17px;
                    top: 71px;
                    left: 49px;
                    z-index: -2;
                }

                &:after {
                    width: 17px;
                    height: 0px;
                    top: 71px;
                    left: 49px;
                    z-index: -1;
                }

                &:hover {
                    &:after {
                        height: 17px;
                    }
                }
            }

            &:nth-child(5) {
                &:before {
                    width: 21px;
                    height: 19px;
                    top: 88px;
                    left: 22px;
                }

                &:after {
                    width: 0px;
                    height: 19px;
                    top: 88px;
                    left: 22px;
                }


                &:hover {
                    &:after {
                        width: 21px;
                    }
                }
            }

            &:nth-child(6) {
                &:before {
                    width: 12px;
                    height: 12px;
                    top: 81px;
                    left: 62px;
