                }

                &:after {
                    width: 0px;
                    height: 0px;
                    top: 87px;
                    left: 68px;
                    border-radius: 50px;
                }

                &:hover {
                    &:after {
                        width: 12px;
                        height: 12px;
                        top: 81px;
                        left: 62px;
                    }
                }
            }
        }



    }
