.contacts {
    p {
        margin-bottom: 40px;
        text-align: center;

        @include sm {
            margin-bottom: 30px;
        }

    }

    .social-items {
        display: flex;
        justify-content: space-around;
        transition: all 0.8s ease;

        /* social item*/
        .social {
            background-color: $neu-06;
            padding: 100 0;
            z-index: 2;
            width: 100%;
            position: relative;
            transition: all 0.6s ease;
            display: flex;
            align-items: center;
            justify-content: center;


            @include sm {
                padding: 50 0;
            }

            @include xs {
                padding: 30 0;
            }

            /*  // Social Texts \\  */
            .social-title {
                color: $neu-03;
                letter-spacing: 10px;
                font-size: 14px;
                position: absolute;
                letter-spacing: 4px;
                transition: all 0.6s ease;
                transform: translateY(-30px);
                opacity: 0;
                font-family: $font-02;
                font-weight: 400;

                @include sm {
                    transform: translateY(30px);
                    opacity: 1;
                    letter-spacing: 2;
                    font-size: 11px;
                    color: $neu-04;
                }

                @include xs {
                    display: none;
                }
            }

            /*  // Icon Social \\  */
            img {
                position: absolute;
                width: 40px;
                height: 40px;
                opacity: 1;
                transition: all 0.6s ease;

                @include sm {
                    top: 20%;
                    width: 30px;
                    height: 30px;
                }

                @include xs {
                    width: 20px;
                    height: 20px;
                    top: initial;
                }

            }

            &:nth-child(even) {
                background-color: $neu-05;
            }

            &:before {
                content: '';
                position: absolute;
                background-color: #313131;
                width: 0%;
                height: 100%;
                z-index: -1;
                top: 0;
                transition: all 0.7s ease;
                color: $neu-03;
            }

            &:after {
                content: '';

                height: 2px;
                border-radius: 20px;
                background-color: $m-01;
                position: absolute;

                animation: lineout 0.6s ease;
                animation-direction: reverse;
                animation-fill-mode: forwards;

                @keyframes lineout {
                    0% {
                        width: 40px;
                        bottom: 60px;
                    }

                    50% {
                        width: 40px;
                        bottom: 0px;
                    }

                    100% {
                        width: 100%;
                        bottom: 0;
                    }
                }

                @include sm {
                    display: none;
                }

            }



            &:hover {
                color: $m-01;
                letter-spacing: 6px;

                &:before {
                    width: 100%;

                    @include sm {
                        display: none;
                    }
                }

                &:after {
                    animation: line 0.8s ease;
                    animation-fill-mode: forwards;

                    @keyframes line {
                        0% {
                            width: 40px;
                            bottom: 60px;
                        }

                        30% {
                            width: 40px;
                            bottom: 0px;
                        }

                        100% {
                            width: 100%;
                            bottom: 0;
                        }
                    }

                    @include sm {
                        display: none;
                    }
                }

                .social-title {
                    opacity: 1;
                    transform: none;
                    letter-spacing: 6px;
                    transition: all 0.6s 0.3s ease;

                    @include sm {
                        transform: translateY(30px);
                        opacity: 1;
                        letter-spacing: 2;
                        font-size: 11px;
                        color: $neu-04;
                    }
                }



                img {
                    width: 100px;
                    height: 100px;
                    opacity: 0.03;

                    @include sm {

                        width: 30px;
                        height: 30px;
                        opacity: 1;

                    }
                }



            }
        }
    }
}

